html {
box-sizing: border-box;
font-size: 16px;
line-height: 1.5;
display:block;

@include responsive(small-phone) {
font-size: .9rem;
}
@include responsive(phone) {
font-size:  .9rem;
}
@include responsive(tablet) {
font-size: 1rem;
}
@include responsive(laptop) {
font-size: 1rem;
}
@include responsive(desktop) {
font-size: 1rem;
}
@include responsive(big-desktop) {
font-size: 1rem;
}

} // end html

body {
display:grid;
grid-template-columns: 1fr;
grid-template-rows:auto auto auto;
font-family: $font-family-reset;
color: $color-text;
font-weight: 400;
line-height: 1.5;
//background-image: linear-gradient(180deg, rgba($mogb, .3), rgba($mogb, .1)), url();
background-color: $color-primary;
$mogb:#4C5D73;
$vligb:#90B0D9 ;
$gwh:#ECEDEC ;
overflow-x:hidden;
box-sizing:border-box;


@include responsive(big-tv){
grid-template-columns: 1fr minmax(auto, 2500px) 1fr;
}

} // end body

.wrapper {
grid-column:1 / -1;
grid-row:1;
display:grid;
width:100%;
grid-template-columns: repeat(12, [col-start] 1fr);
grid-template-rows:auto;

@include responsive(big-tv){
grid-column:2;
}

// .wrapper > * {
// grid-column: col-start / span 12;

// }

& > .sticky {
//position: fixed;
position: sticky;
//left: 0;
//right: 0;
top: 0;
z-index: 10;
}//end sticky

} // end wrapper


.btn{
@include transition;
}

.btn:hover{
//box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.9);
transform: scale(1.05);
}

.end-transitions{
animation-duration:0s !important;
animation-delay:0s !important;
transition-duration: 0s !important;
transition-delay:0s !important;
}
