//mixins
@mixin text($family,$size,$weight,$color) {
font-size:$size;
font-family:$family;
font-weight: $weight;
color:$color;
} //end text

@mixin text-r($family, $size, $weight, $color) {
font-size: $size * 0.85;
font-family:$family;
font-weight: $weight;
color:$color;

@include responsive(phone){
font-size:$size * 0.88 ;
}

@include responsive(tablet){
font-size:$size * 0.9 ;
}

@include responsive(laptop){
font-size: $size * 0.95;
}

@include responsive(desktop){
font-size:$size;//~21
}

@include responsive(big-desktop){
font-size: $size * 1.1 ;//~21
}

@include responsive(developer){
font-size: $size * 1.2 ;//~21
}
} //end text

@mixin text-h($family, $size, $weight, $color) {
font-size: $size * 0.85;
font-family:$family;
font-weight: $weight;
color:$color;

@include responsive(phone){
font-size:$size * 0.88 ;
}

@include responsive(tablet){
font-size:$size * 0.9 ;
}

@include responsive(laptop){
font-size: $size * 0.95;
}

@include responsive(desktop){
font-size:$size;//~21
}

@include responsive(big-desktop){
font-size: $size * 1.1 ;//~21
}

@include responsive(developer){
font-size: $size * 1.2 ;//~21
}
} //end text

h1{
