}

h3{
@include text-h($font-family-base, $med, 600, $color-grayish);
}//end h3

h4{
@include text-h($font-family-base, $big, 600, $color-grayish);
}//end h4


h5{
@include text-h($font-family-base, $less-big, 600, $color-grayish);
}//end h5

