@mixin background-gradient-single-color($img, $color, $screen, $size ) {
background-image: linear-gradient(to right bottom, rgba($color, $screen), rgba($color, $screen)), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin

@mixin background-gradient-rb-double-gradient($img, $color1, $screen1, $color2, $screen2, $size) {
background-image: linear-gradient(to right bottom, rgba($color1, $screen1), rgba($color2, $screen2)), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin


@mixin background-gradient-deg-double-gradient($degrees, $color1, $screen1, $per1, $color2, $screen2, $per2, $size, $img ) {
background-image: linear-gradient($degrees, rgba($color1, $screen1) $per1, rgba($color2, $screen2) $per2 ), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin

